
import { defineComponent, ref } from 'vue';
import { Todo } from '@/types/Todo'

export default defineComponent({
  name: 'Header',
  props: {
    addTodo: {
      type: Function,
      require: true
    }
  },
  setup(prop) {
    const todoTask = ref('')
    
    const add = () => {
      if (!todoTask.value.trim()) return
      const tempTodo: Todo = {
        id: new Date().getTime(),
        title: todoTask.value,
        checked: false
      }
      prop.addTodo && prop.addTodo(tempTodo)
      // 清空文本框
      todoTask.value = ''
    }
    return {
      todoTask,
      add
    }
  }
});
